@use '@styles/colours' as c;

.App {
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
  justify-content: center;
  background: rgb(223 223 223);
}

.Page {
  width: 100%;
  max-width: 550px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.UIContainer {
  padding: 10px 20px 20px 20px;
  display: flex;
  flex-direction: column;
  z-index: 3;
  background: rgb(223 223 223);
}
